import styles from "./Forms.module.css";
import Email from "../../resources/email.png";
import Phone from "../../resources/phone.png";
import Address from "../../resources/address.png";
import { useState, useRef } from "react";
import emailValidator from "email-validator";
import emailjs from "emailjs-com";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export default function Forms() {
  const notification = () => {
    toast.success("formulaire envoy avec succs!", {
      position: "bottom-center",
      autoClose: 4000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const formRef = useRef();
  
  const [nomValeur, setNomValeur] = useState("");
  const [objetValeur, setObjetValeur] = useState("");
  const [emailValeur, setEmailValeur] = useState("");
  const [messageValeur, setMessageValeur] = useState("");
  const [emailTouched, setEmailTouched] = useState(false);
  const [soumettreTouched, setSoumettreTouched] = useState(false);
  const handleNomChange = (event) => {
    setNomValeur(event.currentTarget.value);
    
  };
  const handleObjetChange = (event) => {
    setObjetValeur(event.currentTarget.value);
  };
  const handleEmailChange = (event) => {
    setEmailValeur(event.currentTarget.value);
    setEmailTouched(true);
  };
  const handleMessageChange = (event) => {
    setMessageValeur(event.currentTarget.value);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    setSoumettreTouched(true);
    if (
      emailValeur !== "" &&
      emailValidator.validate(emailValeur) &&
      nomValeur !== "" &&
      objetValeur !== "" &&
      nomValeur.match(/^[aA-zZ\s]+$/) &&
      objetValeur.match(/^[aA-zZ\s]+$/)
    ) {
      emailjs
        .sendForm(
          "service_riad",
          "template_riad",
          formRef.current,
          "FU2fViZ2EfdSB9Hui"
        )
        .then(
          (result) => {
            console.log(result.text);
          },
          (error) => {
            console.log(error.text);
          }
        );
      setSoumettreTouched(false);
      setObjetValeur("");
      setNomValeur("");
      setEmailValeur("");
      setMessageValeur("");
      setEmailTouched(false);
      notification();
    }
  };

  return (
    <div className={styles.c_container}>
      <div className={styles.c_gauche}>
        <h1 className={styles.c_titre}>Besoin de coordonnes?</h1>
        <div className={styles.c_info}>
          <div className={styles.c_info_item}>
            <img src={Phone} className={styles.c_icone} alt="icone telephone" />
            +1 438-836-7072
          </div>
          <div className={styles.c_info_item}>
            <img className={styles.c_icone} src={Email} alt="icone email" />
            riad062008@live.fr
          </div>
          <div className={styles.c_info_item}>
            <img className={styles.c_icone} src={Address} alt="icone adresse" />
            Ottawa, Ontario, CANADA.
          </div>
        </div>
      </div>
      
      <div className={styles.c_droite}>
        <p className={styles.c_desc}>
          <b className={styles.b}>Entrer en contact?</b> Je suis disponible pour rpondre  votre demande de contacte.
        </p>

        <form ref={formRef} onSubmit={handleSubmit}>
          <label>
          <input type="text" value={nomValeur} onChange={handleNomChange} placeholder="Nom complet" name="user_name"/>
          {!nomValeur.match(/^[aA-zZ\s]+$/) && !(nomValeur === "") && (
            <div className={styles.erreur}>
              Le nom ne peut contenir que des lettres alphabetiques.
            </div>
          )}
          {nomValeur === "" && soumettreTouched && (
            <div className={styles.erreur}>
              Le nom n'est pas spcifi.
            </div>
          )}
          </label>

          <label>
            <input type="text" value={objetValeur} onChange={handleObjetChange} placeholder="Objet" name="user_subject"/>
            {!objetValeur.match(/^[aA-zZ\s]+$/) && !(objetValeur === "") && (
            <div className={styles.erreur}>
              L'objet ne peut contenir que des lettres alphabetiques.
            </div>
          )}
          {objetValeur === "" && soumettreTouched && (
            <div className={styles.erreur}>
              L'objet n'est pas spcifi.
            </div>
          )}
          </label>

          <label>
          <input type="text" value={emailValeur} onChange={handleEmailChange} placeholder="Email" name="user_email"/>
          {emailTouched && emailValeur === "" && (
            <div className={styles.erreur}>
              L'adresse courriel n'est pas spcifi.
            </div>
          )}
          {soumettreTouched && !emailTouched && emailValeur === "" && (
            <div className={styles.erreur}>
              L'adresse courriel n'est pas spcifi.
            </div>
          )}
          {!(emailValeur === "") && !emailValidator.validate(emailValeur) && (
            <div className={styles.erreur}>
              L'adresse courriel n'est pas valide.
            </div>
          )}
          </label>

          <label>
            <textarea value={messageValeur} onChange={handleMessageChange} rows="8" placeholder="Votre message" name="message" />
          </label>

          <button type="submit">Soumettre</button>

          <ToastContainer //contenaire de parametres de la notification d'envoie du formulaire
            theme="colored"
            position="bottom-center"
            autoClose={4000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
          />
        </form>
      </div>
    </div>
  );
}
