import { Helmet } from "react-helmet-async";
import Forms from "../../components/Forms/Forms";

export default function Contact() {
  return (
    <div>
      <Helmet>
        <title>Contact - Portfolio Riad</title>
        <meta
          name="description"
          content="Page de contact pour contacter riad, page comportant un formulaire de contact et des coordonnes"
        />
      </Helmet>

      <Forms />
    </div>
  );
}
